<template>
    <div class="content-box">
        <div class="content-wrap">
            <div class="content-dimesion" v-bkloading="{isLoading: isInstanceLoading, opacity: 1}">
                <div class="clearfix">
                    <div class="content-title">{{i18n.taskCategory}}</div>
                    <div class="content-date">
                        <div class="content-date-business">
                            <bk-dropdown-menu ref="dropdown" trigger="click">
                                <span slot="dropdown-trigger">
                                    <span class="dropdown-cursor">{{instanceBusinessName? instanceBusinessName: i18n.chooseBusiness}}</span>
                                    <i :class="['bk-icon icon-angle-down', {'icon-flip': isDropdownShow}]"></i>
