                                :editable="false"
                                @change="onInstanceCategory()">
                            </el-date-picker>
                            <i :class="['bk-icon icon-angle-down', {'icon-flip': chooseDownShow}]"></i>
                        </div>
                    </div>
                </div>
                <data-statistics :dimensionList="taskPlotData" :totalValue="taskTotal"></data-statistics>
            </div>
            <div class="content-wrap-right" v-bkloading="{isLoading: isBuinsessLoading, opacity: 1}">
                <div class="clearfix">
                    <div class="content-title">{{i18n.ownBusiness}}</div>
                    <div class="content-statistics">
                        <div class="content-business">
                            <bk-dropdown-menu ref="dropdown" trigger="click">
                            <span slot="dropdown-trigger">
                                <span class="dropdown-cursor">{{chooseCategoryName? chooseCategoryName : i18n.chooseCategory}}</span>
                                <i :class="['bk-icon icon-angle-down', {'icon-flip': isDropdownShow}]"></i>
                            </span>
                            <ul class="bk-dropdown-list" slot="dropdown-content">
                                <li v-for="item in categoryList" :key="item.value">
                                    <a href="javascript:void(0);" @click="onInstanceBizzCcId(item.value, item.name)">{{item.name}}</a>
                                </li>
                            </ul>
                            </bk-dropdown-menu>
                        </div>
                        <div class="content-business-picker" @click="onInstanceClick">
                            <el-date-picker
                                ref="businessPickerRef"
                                v-model="businessTime"
                                align="center"
                                type="daterange"
                                format="yyyy-MM-dd"
                                value-format="timestamp"
                                :picker-options="pickerOptions"
                                :clearable="false"
                                :editable="false"
                                @change="onInstanceBizzCcId()">
                            </el-date-picker>
                            <i :class="['bk-icon icon-angle-down', {'icon-flip': chooseDownShow}]"></i>
                        </div>
                    </div>
                </div>
                <data-statistics :dimensionList="ownBusinessData" :totalValue="BusinessTotal"></data-statistics>
            </div>
        </div>
        <!-- <div class="content-task-wrap"> -->
            <!-- <div class="clearfix">
            <div class="p20">{{i18n.taskInstance}}</div>
                <div class="content-task-instance">
                <div class="content-instance-month">
                <bk-dropdown-menu ref="dropdown">
                    <span slot="dropdown-trigger">
                    <span>{{i18n.taskDimension}}</span>
                    <i :class="['bk-icon icon-angle-down',{'icon-flip': isDropdownShow}]"></i>
