            </div>
            </div> -->
            <!-- <data-statistics :dimensionList="taskInstanceData"></data-statistics> -->
            <!-- 任务实例接口 -->
        <!-- </div> -->
        <div class="content-process-detail">
            <bk-tab :type="'fill'" :active-name="tabName" @tab-changed="onChangeTabPanel">
                <bk-tabpanel name="taskDetails" :title="i18n.taskDetail">
                    <div class="content-wrap-detail">
                        <div class="content-wrap-from">
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.timeLimit}}</label>
                                <el-date-picker
                                    v-model="time"
                                    align="center"
                                    type="daterange"
                                    format="yyyy-MM-dd"
                                    value-format="timestamp"
                                    :picker-options="pickerOptions"
                                    :clearable="false"
                                    :editable="false"
                                    @change="onInstanceNode">
                                </el-date-picker>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseBusiness}}</label>
                                <el-select
                                    v-model="bizCcId"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onInstanceNode">
                                    <el-option
                                        v-for="item in bizList"
                                        :key="item.cc_id"
                                        :label="item.cc_name"
                                        :value="item.cc_id">
                                    </el-option>
                                </el-select>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseCategory}}</label>
                                <el-select
                                    v-model="category"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onInstanceNode">
                                    <el-option
                                        v-for="item in categorys"
                                        :key="item.value"
                                        :label="item.name"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                        <data-table-pagination
                            v-bkloading="{isLoading: isDetailsLoading, opacity: 1}"
                            :data="nodeData"
                            :total="nodeTotal"
                            :options="dataTableOptions"
                            :pagination="nodePagination"
                            :columns="nodeColumns"
                            @handleSortChange="onNodeSortChange"
                            @handleSizeChange="onNodeHandleSizeChange"
                            @handleIndexChange="onNodeHandleIndexChange">
                        </data-table-pagination>
                    </div>
                </bk-tabpanel>
                <bk-tabpanel name="exectionTime" :title="i18n.executionName">
                    <div class="content-wrap-detail">
                        <div class="content-wrap-from">
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.timeLimit}}</label>
                                <el-date-picker
                                    v-model="time"
                                    align="center"
                                    type="daterange"
                                    format="yyyy-MM-dd"
                                    value-format="timestamp"
                                    :picker-options="pickerOptions"
                                    :clearable="false"
                                    :editable="false"
                                    @change="onInstanceDetailsData">
                                </el-date-picker>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseBusiness}}</label>
                                <el-select
                                    v-model="bizCcId"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onInstanceDetailsData">
                                    <el-option
                                        v-for="item in bizList"
                                        :key="item.cc_id"
                                        :label="item.cc_name"
                                        :value="item.cc_id">
                                    </el-option>
                                </el-select>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseCategory}}</label>
                                <el-select
                                    v-model="category"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onInstanceDetailsData">
                                    <el-option
                                        v-for="item in categorys"
                                        :key="item.value"
                                        :label="item.name"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                        <data-table-pagination
                            v-bkloading="{isLoading: isExectionLoading, opacity: 1}"
                            :data="detailsData"
                            :total="detailsTotal"
                            :pagination="detailsPagination"
                            :columns="detailsColumns"
                            @handleSizeChange="onDetailsHandleSizeChange"
                            @handleIndexChange="onDetailsHandleIndexChange">
                        </data-table-pagination>
                    </div>
                </bk-tabpanel>
            </bk-tab>
        </div>
    </div>
</template>
<script>
import '@/utils/i18n.js'
import tools from '@/utils/tools.js'
import DataStatistics from '../dataStatistics/index.vue'
import {mapActions, mapState} from 'vuex'
import {AnalysisMixins} from '@/mixins/js/analysisMixins.js'
import DataTablePagination from '@/components/common/dataTable/DataTablePagination.vue'
import { errorHandler } from '@/utils/errorHandler.js'
export default {
    name: 'StatisticsInstance',
    components: {
        DataStatistics,
        DataTablePagination
    },
    mixins: [AnalysisMixins],
    data () {
        return {
            i18n: {
                taskCategory: gettext('任务分类'),
                ownBusiness: gettext('所属业务'),
                taskDetail: gettext('任务详情'),
                executionName: gettext('执行耗时'),
                timeLimit: gettext('时间范围'),
                chooseCategory: gettext('选择分类'),
                chooseBusiness: gettext('选择业务'),
                chooseTime: gettext('选择时间'),
                taskInstance: gettext('任务实例'),
                taskDimension: gettext('时间维度'),
                choice: gettext('请选择')
            },
            bizCcId: '',
            category: '',
            instanceBusinessName: '',
            chooseCategoryName: '',
            datePickerRefShow: false,
            businessPickerRefShow: false,
            isDropdownShow: false,
            chooseDownShow: false,
            isInstanceLoading: true,
            isBuinsessLoading: true,
            isExectionLoading: true,
            isDetailsLoading: true,
            time: [0, 0],
            businessTime: [0, 0],
            taskBusineTime: [0, 0],
            taskPlotData: [],
            ownBusinessData: [],
            taskInstanceData: [],
            nodeData: [],
            nodeTotal: 0,
            taskTotal: 0,
            BusinessTotal: 0,
            nodePageIndex: 1,
            nodeLimit: 15,
            nodeOrderBy: '-instanceId',
            detailsOrderBy: '-instanceId',
            tabName: 'taskDetails',
            nodePagination: {
                // 分页操作
                limit: this.nodeLimit,
                pageIndex: this.nodePageIndex,
                pageArray: this.dataTablePageArray // 公共js文件获取
            },
            nodeColumns: [
                {
                    prop: 'instanceName',
                    label: gettext('任务名称'),
                    width: '285',
                    title: 'instanceName',
                    router: (row, column, cellValue,index) => {
                        return `/taskflow/execute/${row.businessId}/?instance_id=${row.instanceId}`
                    },
                    formatter: (row, column, cellValue,index) => {
                        return `<a class="template-router" href="/taskflow/execute/${row.businessId}/?instance_id=${row.instanceId}">${row.instanceName}</a>`
                    }
                },
                {
                    prop: 'businessName', // 识别id
                    label: gettext('所属业务'), // 表头显示名称
                    align: 'center' // 对其格式，可选（right，left，center）
                },
                {
                    prop: 'createTime',
                    label: gettext('创建时间'),
                    align: 'center'
                },
                {
                    prop: 'creator',
                    label: gettext('创建人'),
                    align: 'center'
                },
                {
                    prop: 'category',
                    label: gettext('分类'),
                    align: 'center'
                },
                {
                    prop: 'atomTotal',
                    label: gettext('原子个数'),
                    sortable: 'custom',
                    align: 'center'
                },
                {
                    prop: 'subprocessTotal',
                    label: gettext('子流程个数'),
                    sortable: 'custom',
                    align: 'center'
                },
                {
                    prop: 'gatewaysTotal',
                    label: gettext('网关个数'),
                    sortable: 'custom',
                    align: 'center'
                }
            ],
            detailsData: [],
            detailsTotal: 0,
            detailsPageIndex: 1,
            detailsLimit: 15,
            detailsPagination: {
                limit: this.detailsLimit,
                pageIndex: this.detailsPageIndex,
                pageArray: this.dataTablePageArray
            },
            taskDimensionArray: [
                {
                    label: gettext('日'),
                    value: 'day'
                },
                {
                    label: gettext('月'),
                    value: 'month'
                }
            ],
            detailsColumns: [
                {
                    prop: 'instanceName',
                    label: gettext('任务名称'),
                    title: 'instanceName',
                    router: (row, column, cellValue,index) => {
                        return `/taskflow/execute/${row.businessId}/?instance_id=${row.instanceId}`
                    },
                    formatter: (row, column, cellValue,index) => {
                        return `<a class="template-router" href="/taskflow/execute/${row.businessId}/?instance_id=${row.instanceId}">${row.instanceName}</a>`
                    }
                },
                {
                    prop: 'businessName',
                    label: gettext('所属业务'),
                    align: 'center'
                },
                {
                    prop: 'category',
                    label: gettext('分类'),
                    align: 'center'
                },
                {
                    prop: 'executeTime',
                    label: gettext('耗时（秒）'),
                    align: 'center'
                },
                {
                    prop: 'creator',
                    label: gettext('创建人'),
                    align: 'center'
                }
            ],
            instanceType: 'day'
        }
    },
    created () {
        this.getDateTime()
    },
    mounted () {
        this.onInstanceCategory()
        this.onInstanceNode()
        this.onInstanceBizzCcId()
    },
    computed: {
        ...mapState({
            bizList: state => state.bizList,
            instanceList: state => state.bizList,
            categorys: state => state.categorys,
            site_url: state => state.site_url
        }),
        businessList () {
            if (this.bizList.length === 0) {
                this.getBizList()
            }
            const list = tools.deepClone(this.bizList)
            list.unshift({cc_id: undefined, cc_name: gettext('全部')})
            return list
        },
        categoryList () {
            if (this.categorys.length === 0) {
                this.getCategorys()
            }
            const list = tools.deepClone(this.categorys)
            list.unshift({value: undefined, name: gettext('全部')})
            return list
        }
    },
    methods: {
        ...mapActions('task/', [
            'queryInstanceData'
        ]),
        ...mapActions([
            'getBizList',
            'getCategorys'
        ]),
        onNodeHandleSizeChange (limit) {
            this.nodePageIndex = 1
            this.nodeLimit = limit
            this.onInstanceNode()
        },
        onNodeHandleIndexChange (pageIndex) {
            this.nodePageIndex = pageIndex
            this.onInstanceNode()
        },
        onNodeSortChange (column, prop, order) {
            order = column[0].order === 'ascending' ? '' : '-'
            this.nodeOrderBy = column[0].prop ?  order + column[0].prop : '-instanceId'
            this.onInstanceNode()
        },
        onInstanceCategory (business, name) {
            if (!Array.isArray(name)) {
                this.instanceName = name
            }
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'category',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    biz_cc_id: business
                })
            }
            this.instanceData(data)
        },
        onInstanceBizzCcId (category, name) {
            if (!Array.isArray(name)) {
                this.chooseCategoryName = name
            }
            let time = this.getUTCTime(this.businessTime)
            const data = {
                group_by: 'biz_cc_id',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    category: category
                })
            }
            this.plotBizidData(data)
        },
        onInstanceNode () {
            this.isDetailsLoading = true
            try {
                let time = this.getUTCTime(this.time)
                const data = {
                    group_by: 'instance_node',
                    conditions: JSON.stringify({
                        create_time: time[0],
                        finish_time: time[1],
                        biz_cc_id: this.bizCcId,
                        category: this.category,
                        order_by: this.nodeOrderBy
                    }),
                    pageIndex: this.nodePageIndex,
                    limit: this.nodeLimit
                }
                this.instanceDataTableData(data)
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.isDetailsLoading = false
            }
        },
        // dataTableInstanceTime (name) {
        //     if (!Array.isArray(name) && typeof name === 'string') {
        //         this.i18n.taskDimension = name
        //     }
        //     let time = this.getUTCTime(this.taskBusineTime)
        //     const data = {
        //         group_by: 'instance_time',
        //         type: this.instanceType,
        //         conditions: JSON.stringify({
        //             create_time: time[0],
        //             finish_time: time[1],
        //             biz_cc_id: this.bizCcId,
        //             category: this.category
        //         })
        //     }
        //     this.instanceTimeData(data)
        // },
        async instanceDataTableData (data) {
            try {
                const templateData = await this.queryInstanceData(data)
                switch (data.group_by) {
                    case 'instance_node':
                        this.nodeData = templateData.data.groups
                        this.nodeTotal = templateData.data.total
                        break
                    case 'instance_details':
                        this.detailsData = templateData.data.groups
                        this.detailsTotal = templateData.data.total
                        break
                }
            }  catch (e) {
                errorHandler(e, this)
            }
        },
        async plotBizidData (data) {
            this.isBuinsessLoading = true
            try {
                const templateData = await this.queryInstanceData(data)
                this.ownBusinessData = templateData.data.groups
                this.BusinessTotal = templateData.data.total
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.isBuinsessLoading = false
            }
        },
        async instanceTimeData (data) {
            try {
                const templateData = await this.queryInstanceData(data)
                this.taskInstanceData = templateData.data.groups
            } catch (e) {
                errorHandler(e, this)
            }
        },
        async instanceData (data) {
            this.isInstanceLoading = true
            try {
                const templateData = await this.queryInstanceData(data)
                this.taskPlotData = templateData.data.groups
                this.taskTotal = templateData.data.total
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.isInstanceLoading = false
            }
        },
        onDetailsHandleSizeChange (limit) {
            this.detailsPageIndex = 1
            this.detailsLimit = limit
            this.onInstanceDetailsData()
        },
        onDetailsHandleIndexChange (pageIndex) {
            this.detailsPageIndex = pageIndex
            this.onInstanceDetailsData()
        },
        detailsHandleSortChange (column, prop, order) {
            order = column[0].order === 'ascending' ? '' : '-'
            this.detailsOrderBy = column[0].prop ?  order + column[0].prop : '-instanceId'
            this.onInstanceDetailsData()
        },
        onInstanceDetailsData () {
            this.isExectionLoading = true
            try {
                let time = this.getUTCTime(this.time)
                const data = {
                    group_by: 'instance_details',
                    conditions: JSON.stringify({
                        create_time: time[0],
                        finish_time: time[1],
                        biz_cc_id: this.bizCcId,
                        category: this.category,
                        order_by: this.detailsOrderBy
                    }),
                    pageIndex: this.detailsPageIndex,
                    limit: this.detailsLimit
                }
                this.instanceDataTableData(data)
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.isExectionLoading = false
            }
        },
        onChangeTabPanel (name) {
            this.tabName = name
            if (name === 'taskDetails') {
                this.onInstanceNode()
            } else {
                this.onInstanceDetailsData()
            }
        },
        getDateTime () {
            let date = new Date()
            date.setHours(0, 0, 0)
            this.time[1] = Date.parse(date)
            this.businessTime[1] = Date.parse(date)
            this.taskBusineTime[1] = Date.parse(date)
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 30)
            this.time[0] = Date.parse(date)
            this.businessTime[0] = Date.parse(date)
            this.taskBusineTime[0] = Date.parse(date)
        },
        onDatePickerClick () {
            this.datePickerRefShow = !this.datePickerRefShow
            this.$refs.datePickerRef.pickerVisible = this.datePickerRefShow
        },
        onInstanceClick () {
            this.businessPickerRefShow = !this.businessPickerRefShow
            this.$refs.businessPickerRef.pickerVisible = this.businessPickerRefShow
        }
    }
}
</script>
